import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';

import BigNumber from 'bignumber.js';
import {
  getBalanceBonded,
  getBalanceOfStaged, getFluidUntil, getLockedUntil,
  getStatusOf, getTokenAllowance,
  getTokenBalance, getTokenTotalSupply,
} from '../../utils/infura';
import {BSD, BSDS} from "../../constants/tokens";
import { toTokenUnitsBN } from '../../utils/number';

import AccountPageHeader from "./Header";
import WithdrawDeposit from "./WithdrawDeposit";
import BondUnbond from "./BondUnbond";
import IconHeader from "../common/IconHeader";
import {getPoolAddress} from "../../utils/pool";
import {DollarPool4} from "../../constants/contracts";
import { DAO_EXIT_LOCKUP_EPOCHS } from '../../constants/values';

function Wallet({ user }: {user: string}) {
  // @ts-ignore
  const { override } = useParams();
  if (override) {
    user = override;
  }

  const [userBSDBalance, setUserBSDBalance] = useState(new BigNumber(0));
  const [userBSDAllowance, setUserBSDAllowance] = useState(new BigNumber(0));
  const [userBSDSBalance, setUserBSDSBalance] = useState(new BigNumber(0));
  const [totalBSDSSupply, setTotalBSDSSupply] = useState(new BigNumber(0));
  const [userStagedBalance, setUserStagedBalance] = useState(new BigNumber(0));
  const [userBondedBalance, setUserBondedBalance] = useState(new BigNumber(0));
  const [userStatus, setUserStatus] = useState(0);
  const [userStatusUnlocked, setUserStatusUnlocked] = useState(0);
  const [lockup, setLockup] = useState(0);

  //Update User balances
  useEffect(() => {
    if (user === '') {
      setUserBSDBalance(new BigNumber(0));
      setUserBSDAllowance(new BigNumber(0));
      setUserBSDSBalance(new BigNumber(0));
      setTotalBSDSSupply(new BigNumber(0));
      setUserStagedBalance(new BigNumber(0));
      setUserBondedBalance(new BigNumber(0));
      setUserStatus(0);
      return;
    }
    let isCancelled = false;

    async function updateUserInfo() {
      const [
        esdBalance, esdAllowance, BSDSBalance, BSDSSupply, stagedBalance, bondedBalance, status, poolAddress,
        fluidUntilStr, lockedUntilStr
      ] = await Promise.all([
        getTokenBalance(BSD.addr, user),
        getTokenAllowance(BSD.addr, user, BSDS.addr),
        getTokenBalance(BSDS.addr, user),
        getTokenTotalSupply(BSDS.addr),
        getBalanceOfStaged(BSDS.addr, user),
        getBalanceBonded(BSDS.addr, user),
        getStatusOf(BSDS.addr, user),
        getPoolAddress(),

        getFluidUntil(BSDS.addr, user),
        getLockedUntil(BSDS.addr, user),
      ]);

      const userBSDBalance = toTokenUnitsBN(esdBalance, BSD.decimals);
      const userBSDSBalance = toTokenUnitsBN(BSDSBalance, BSDS.decimals);
      const totalBSDSSupply = toTokenUnitsBN(BSDSSupply, BSDS.decimals);
      const userStagedBalance = toTokenUnitsBN(stagedBalance, BSDS.decimals);
      const userBondedBalance = toTokenUnitsBN(bondedBalance, BSDS.decimals);
      const userStatus = parseInt(status, 10);
      const fluidUntil = parseInt(fluidUntilStr, 10);
      const lockedUntil = parseInt(lockedUntilStr, 10);

      if (!isCancelled) {
        setUserBSDBalance(new BigNumber(userBSDBalance));
        setUserBSDAllowance(new BigNumber(esdAllowance));
        setUserBSDSBalance(new BigNumber(userBSDSBalance));
        setTotalBSDSSupply(new BigNumber(totalBSDSSupply));
        setUserStagedBalance(new BigNumber(userStagedBalance));
        setUserBondedBalance(new BigNumber(userBondedBalance));
        setUserStatus(userStatus);
        setUserStatusUnlocked(Math.max(fluidUntil, lockedUntil))
        setLockup(poolAddress == DollarPool4 ? DAO_EXIT_LOCKUP_EPOCHS : 1);
      }
    }
    updateUserInfo();
    const id = setInterval(updateUserInfo, 15000);

    // eslint-disable-next-line consistent-return
    return () => {
      isCancelled = true;
      clearInterval(id);
    };
  }, [user]);

  return (
    <>
      <div style={{ color: 'rgba(255, 255, 255, 0.8)' }}>
        <IconHeader icon={<i className="fas fa-dot-circle"/>} text="DAO"/>
      </div>

      <AccountPageHeader
        accountBSDBalance={userBSDBalance}
        accountBSDSBalance={userBSDSBalance}
        totalBSDSSupply={totalBSDSSupply}
        accountStagedBalance={userStagedBalance}
        accountBondedBalance={userBondedBalance}
        accountStatus={userStatus}
        unlocked={userStatusUnlocked}
      />

      <WithdrawDeposit
        user={user}
        balance={userBSDBalance}
        allowance={userBSDAllowance}
        stagedBalance={userStagedBalance}
        status={userStatus}
      />

      <BondUnbond
        staged={userStagedBalance}
        bonded={userBondedBalance}
        status={userStatus}
        lockup={lockup}
      />
    </>
  );
}

export default Wallet;
