import React from 'react';
import BigNumber from 'bignumber.js';
import styled from 'styled-components';
import { BalanceBlock } from '../common/index';
import TextBlock from "../common/TextBlock";
import {ownership} from "../../utils/number";
import BoxStyle from '../common/BoxStyle';

type PoolPageHeaderProps = {
  accountUNIBalance: BigNumber,
  accountBondedBalance: BigNumber,
  accountRewardedBSDBalance: BigNumber,
  accountClaimableBSDBalance: BigNumber,
  poolTotalBonded: BigNumber,
  accountPoolStatus: number,
};

const STATUS_MAP = ["Frozen", "Fluid"];

const Container = styled.div`
  ${BoxStyle}
  margin-bottom: 32px;
`;

const PoolPageHeader = ({
  accountUNIBalance, accountBondedBalance, accountRewardedBSDBalance, accountClaimableBSDBalance, poolTotalBonded, accountPoolStatus
}: PoolPageHeaderProps) => (
  <Container style={{ padding: '2%', display: 'flex', flexWrap: 'wrap', alignItems: 'center' }}>
    <div style={{ flexBasis: '20%' }}>
      <BalanceBlock asset="Balance" balance={accountUNIBalance}  suffix={" UNI-V2"}/>
    </div>
    <div style={{ flexBasis: '20%' }}>
      <BalanceBlock asset="Rewarded" balance={accountRewardedBSDBalance} suffix={" BSD"} />
    </div>
    <div style={{ flexBasis: '20%' }}>
      <BalanceBlock asset="Claimable" balance={accountClaimableBSDBalance} suffix={" BSD"} />
    </div>
    <div style={{ flexBasis: '20%' }}>
      <BalanceBlock asset="Pool Ownership" balance={ownership(accountBondedBalance, poolTotalBonded)}  suffix={"%"}/>
    </div>
    <div style={{ flexBasis: '20%' }}>
      <TextBlock label="Pool Status" text={STATUS_MAP[accountPoolStatus]}/>
    </div>
  </Container>
);


export default PoolPageHeader;
