const BoxStyle = `
  transition: transform 100ms ease-in-out;
  will-change: transform;
  color: #002884;
  box-shadow: 6px 6px 0px #3f50b5;
  padding: 4px 8px;
  border: 4px solid rgba(255, 255, 255, 0.9);
  border-radius: 0;
  outline: none;
  background: #fff;
`;

export default BoxStyle;
