import React, {useCallback, useEffect, useState} from 'react';
import {
  Button
} from '@aragon/ui';
import {advance, connect} from "../../utils/web3";
import {BSDS} from "../../constants/tokens";
import BigNumber from "bignumber.js";
import {getEpoch, getEpochTime} from "../../utils/infura";

type EpochBlockProps = {
  epoch: string
}

function EpochBlock({ epoch }: EpochBlockProps) {
  const [epochCurrent, setEpochCurrent] = useState(new BigNumber(0));
  const [epochTime, setEpochTime] = useState(new BigNumber(0));

  useEffect(() => {
    const fetch = async () => {
      const [ fetchedEpoch, fetchedEpochTime ] = await Promise.all([
        getEpoch(BSDS.addr),
        getEpochTime(BSDS.addr)
      ]);

      setEpochCurrent(new BigNumber(fetchedEpoch));
      setEpochTime(new BigNumber(fetchedEpochTime));
    }

    fetch();

    const fetchInterval = setInterval(fetch, 10000);
    return () => clearInterval(fetchInterval);
  }, [setEpochCurrent, setEpochTime]);

  const handleAdvance = useCallback(async () => {
    await connect();
    await advance(BSDS.addr);
  }, []);

  return (
    <>
      <div style={{ fontSize: 16, padding: 3 }}>Epoch</div>
      <div style={{ fontSize: 24, padding: 3, fontWeight: 400, lineHeight: 1.5, fontFamily: 'aragon-ui-monospace, monospace'}}>{epoch}</div>
      <Button
        wide
        label="Advance"
        onClick={async () => { await handleAdvance(); }}
        disabled={epochTime.eq(0) || epochTime.lte(epochCurrent)}
      />
    </>
  );
}

export default EpochBlock;
