import React, { useState, useEffect } from 'react';
import { LinkBase, Box } from '@aragon/ui';
import styled from 'styled-components';
import BigNumber from 'bignumber.js';
import { getTokenBalance } from '../../utils/infura';
import { toTokenUnitsBN } from '../../utils/number';

import TradePageHeader from './Header';
import {BSD, UNI, USDC} from "../../constants/tokens";
import IconHeader from "../common/IconHeader";
import BoxStyle from '../common/BoxStyle';

const Container = styled(Box)`${BoxStyle}`;

function UniswapPool({ user }: {user: string}) {
  const [pairBalanceBSD, setPairBalanceBSD] = useState(new BigNumber(0));
  const [pairBalanceUSDC, setPairBalanceUSDC] = useState(new BigNumber(0));

  useEffect(() => {
    let isCancelled = false;

    async function updateUserInfo() {
      const [
        pairBalanceBSDStr, pairBalanceUSDCStr,
      ] = await Promise.all([
        getTokenBalance(BSD.addr, UNI.addr),
        getTokenBalance(USDC.addr, UNI.addr),
      ]);

      if (!isCancelled) {
        setPairBalanceBSD(toTokenUnitsBN(pairBalanceBSDStr, BSD.decimals));
        setPairBalanceUSDC(toTokenUnitsBN(pairBalanceUSDCStr, USDC.decimals));
      }
    }

    updateUserInfo();
    const id = setInterval(updateUserInfo, 15000);

    // eslint-disable-next-line consistent-return
    return () => {
      isCancelled = true;
      clearInterval(id);
    };
  }, [user]);

  return (
    <>
      <div style={{ color: 'rgba(255, 255, 255, 0.8)' }}>
        <IconHeader icon={<i className="fas fa-exchange-alt"/>} text="Trade"/>
      </div>

      <TradePageHeader
        pairBalanceBSD={pairBalanceBSD}
        pairBalanceUSDC={pairBalanceUSDC}
        uniswapPair={UNI.addr}
      />

      <div style={{ display: 'flex', flexWrap: 'wrap', alignItems: 'center' }}>
        <div style={{ flexBasis: '30%', marginRight: '5%'  }}>
          <MainButton
            title="Info"
            description="View BSD-USDC pool stats."
            icon={<i className="fas fa-chart-area"/>}
            href={"https://uniswap.info/pair/0x043F62e75D3e74f47a91408a8376bf00391Aa227"}
          />
        </div>

        <div style={{ flexBasis: '30%' }}>
          <MainButton
            title="Trade"
            description="Trade BSD tokens."
            icon={<i className="fas fa-exchange-alt"/>}
            href={"https://uniswap.exchange/swap?inputCurrency=0xa0b86991c6218b36c1d19d4a2e9eb0ce3606eb48&outputCurrency=0x0afB6a6ba319202464c75eD09F2010d883257cA9"}
          />
        </div>

        <div style={{ flexBasis: '30%', marginLeft: '5%' }}>
          <MainButton
            title="Supply"
            description="Supply and redeem liquidity."
            icon={<i className="fas fa-water"/>}
            href={"https://uniswap.exchange/add/0x0afB6a6ba319202464c75eD09F2010d883257cA9/0xa0b86991c6218b36c1d19d4a2e9eb0ce3606eb48"}
          />
        </div>
      </div>
    </>
  );
}

type MainButtonProps = {
  title: string,
  description: string,
  icon: any,
  href:string
}

function MainButton({
  title, description, icon, href,
}:MainButtonProps) {
  return (
    <LinkBase href={href} style={{ width: '100%' }}>
      <Container>
        <div style={{ padding: 10, fontSize: 18 }}>
          {title}
        </div>
        <span style={{ fontSize: 48 }}>
          {icon}
        </span>
        {/*<img alt="icon" style={{ padding: 10, height: 64 }} src={iconUrl} />*/}
        <div style={{ paddingTop: 5, opacity: 0.5 }}>
          {' '}
          {description}
          {' '}
        </div>

      </Container>
    </LinkBase>
  );
}

export default UniswapPool;
