import React from 'react';
import BigNumber from 'bignumber.js';
import styled from 'styled-components';
import { BalanceBlock, AddressBlock } from '../common/index';
import BoxStyle from '../common/BoxStyle';

type TradePageHeaderProps = {
  pairBalanceBSD: BigNumber,
  pairBalanceUSDC: BigNumber,
  uniswapPair: string,
};

const Container = styled.div`${BoxStyle} margin-bottom: 32px;`;

const TradePageHeader = ({
  pairBalanceBSD, pairBalanceUSDC, uniswapPair,
}: TradePageHeaderProps) => {
  const price = pairBalanceUSDC.dividedBy(pairBalanceBSD);

  return (
    <Container style={{ padding: '2%', display: 'flex', flexWrap: 'wrap', alignItems: 'center' }}>
      <div style={{ flexBasis: '25%' }}>
        <BalanceBlock asset="BSD Price" balance={price} suffix={"USDC"}/>
      </div>
      <div style={{ flexBasis: '25%' }}>
        <BalanceBlock asset="BSD Liquidity" balance={pairBalanceBSD} suffix={"BSD"}/>
      </div>
      <div style={{ flexBasis: '25%' }}>
        <BalanceBlock asset="USDC Liquidity" balance={pairBalanceUSDC} suffix={"USDC"}/>
      </div>
      <div style={{ flexBasis: '25%' }}>
        <>
          <AddressBlock label="Uniswap Contract" address={uniswapPair} />
        </>
      </div>
    </Container>
  );
}


export default TradePageHeader;
