import React, {useEffect, useState} from 'react';
import { useHistory } from 'react-router-dom';
import styled from 'styled-components';
import {
  DataView, Button, IconCirclePlus, Box
} from '@aragon/ui';

import {getBatchBalanceOfCoupons, getBatchCouponsExpiration, getCouponEpochs} from '../../utils/infura';
import {BSD, BSDS} from "../../constants/tokens";
import {formatBN, toBaseUnitBN, toTokenUnitsBN} from "../../utils/number";
import BigNumber from "bignumber.js";
import {redeemCoupons} from "../../utils/web3";
import BoxStyle from '../common/BoxStyle';

type PurchaseHistoryProps = {
  user: string,
  hideRedeemed: boolean,
  totalRedeemable: BigNumber
};

const Container = styled(Box)`${BoxStyle}`;

function PurchaseHistory({
  user, hideRedeemed, totalRedeemable
}: PurchaseHistoryProps) {
  const history = useHistory();
  const [epochs, setEpochs] = useState([]);
  const [page, setPage] = useState(0)
  const [initialized, setInitialized] = useState(false)

  //Update User balances
  useEffect(() => {
    if (user === '') return;
    let isCancelled = false;

    async function updateUserInfo() {
      const epochsFromEvents = await getCouponEpochs(BSDS.addr, user);
      const epochNumbers = epochsFromEvents.map(e => parseInt(e.epoch));
      const balanceOfCoupons = await getBatchBalanceOfCoupons(BSDS.addr, user, epochNumbers);
      const couponsExpirations = await getBatchCouponsExpiration(BSDS.addr, epochNumbers);

      const couponEpochs = epochsFromEvents.map((epoch, i) => {
        epoch.balance = new BigNumber(balanceOfCoupons[i]);
        epoch.expiration = couponsExpirations[i];
        return epoch;
      });

      if (!isCancelled) {
        // @ts-ignore
        setEpochs(couponEpochs);
        setInitialized(true);
      }
    }
    updateUserInfo();
    const id = setInterval(updateUserInfo, 15000);

    // eslint-disable-next-line consistent-return
    return () => {
      isCancelled = true;
      clearInterval(id);
    };
  }, [user, totalRedeemable]);

  return (
    <Container>
      <DataView
        fields={['Epoch', 'Purchased', 'Balance', 'Expires', '']}
        status={ initialized ? 'default' : 'loading' }
        // @ts-ignore
        entries={hideRedeemed ? epochs.filter((epoch) => !epoch.balance.isZero()) : epochs}
        entriesPerPage={10}
        page={page}
        onPageChange={setPage}
        renderEntry={(epoch) => [
          epoch.epoch.toString(),
          formatBN(toTokenUnitsBN(epoch.coupons, BSD.decimals), 2),
          formatBN(toTokenUnitsBN(epoch.balance, BSD.decimals), 2),
          epoch.expiration.toString(),
          <Button
            icon={<IconCirclePlus />}
            label="Redeem"
            onClick={() => redeemCoupons(
              BSDS.addr,
              epoch.epoch,
              toBaseUnitBN(
                epoch.balance.isGreaterThan(totalRedeemable) ? totalRedeemable : epoch.balance,
                BSD.decimals
              ),
            )}
            disabled={epoch.balance.isZero()}
          />
        ]}
      />
    </Container>
  );
}

export default PurchaseHistory;
