import React from 'react';
import styled from 'styled-components';
import { Header as UIHeader } from '@aragon/ui';

interface Props {
  primary: string;
}

const HeaderWrapper = styled(UIHeader)`
  h1 {
    color: rgba(255, 255, 255, 0.8) !important;
  }
`;

function Header({ primary }: Props): React.ReactElement {
  return (
    <HeaderWrapper primary={primary} />
  );
}

export default Header;
