import React from 'react';
import styled from 'styled-components';
import BoxStyle from '../common/BoxStyle';
import NumberBlock from "../common/NumberBlock";

type CommitHeaderProps = {
  epoch: number
  startEpoch: number,
  periodEpoch: number,
};

const Container = styled.div`${BoxStyle}`;

const CommitHeader = ({
   epoch, startEpoch, periodEpoch,
}: CommitHeaderProps) => (
  <Container style={{ padding: '2%', display: 'flex', alignItems: 'center' }}>
    <div style={{ width: '25%' }}>
      <NumberBlock title="Epoch" num={epoch} />
    </div>
    <div style={{ width: '25%' }}>
      <NumberBlock title="Starts" num={startEpoch} />
    </div>
    <div style={{ width: '25%' }}>
      <NumberBlock title="Period" num={periodEpoch} />
    </div>
  </Container>
);


export default CommitHeader;
